<template>
	<div class="modal" :name="name" :class="{ '_opened': isModalOpened }">
		<div class="modal__header">
			{{ title }}
		</div>
		<div class="modal__body">
			<slot></slot>
		</div>
	</div>
</template>

<script>
	import { mapGetters } from 'vuex';

	export default {
		name: "Modal",

		props: {
			name: {
				type: String,
				default: 'default-modal'
			},
			title: {
				type: String,
			}
		},

		computed: {
			...mapGetters([
				'isModalOpened',
			])
		}
	}
</script>

<style lang="less" scoped>
	@import "../css/_vars";

	.modal {
		position: fixed;
		width: 100vw;
		height: 100vh;
		top: -100vh;
		left: 0;
		background-color: @color-dark;
		transition: top 0.4s ease-in-out;
		z-index: 1;

		&._opened {
			top: 0;
		}

		&__header {

		}

		&__body {

		}
	}
</style>


